<template>
  <div v-if="currentSong && currentSong.id && currentSong.id !== 0" class="mini-player">
    <!-- 进度条 -->
    <div class="progress-container">
      <div 
        class="progress-bar"
        :style="{ width: progressPercent + '%' }"
      ></div>
    </div>
    
    <div class="mini-player-content" @click="openFullPlayer">
      <!-- 歌曲封面 -->
      <div class="cover-container">
        <div class="cover">
          <img 
            :src="currentSong.cover || '/images/album.jpg'" 
            :alt="currentSong.name"
            @error="handleImageError"
          />
          <!-- 播放状态指示器 -->
          <div v-if="isPlaying" class="play-indicator">
            <div class="wave-bar"></div>
            <div class="wave-bar"></div>
            <div class="wave-bar"></div>
            <div class="wave-bar"></div>
          </div>
        </div>
      </div>
      
      <!-- 歌曲信息 -->
      <div class="song-info">
        <div class="song-name">{{ currentSong.name }}</div>
        <div class="song-meta">
          <span class="artist-name">{{ currentSong.artists?.map(a => a.name).join(', ') }}</span>
          <span class="time-info">{{ formatTime(musicStore.currentTime) }} / {{ formatTime(musicStore.duration) }}</span>
        </div>
      </div>
    </div>
    
    <!-- 控制按钮区域 -->
    <div class="controls-section">
      <!-- 主要控制按钮 -->
      <div class="main-controls">
        <!-- 上一首 -->
        <button 
          class="control-btn secondary"
          @click.stop="prevSong"
          :disabled="!musicStore.hasPrev"
        >
          <IonIcon :icon="playSkipBackOutline" />
        </button>
        
        <!-- 播放/暂停 -->
        <button 
          class="control-btn primary play-pause-btn"
          @click.stop="togglePlay"
        >
          <IonIcon :icon="isPlaying ? pause : play" />
        </button>
        
        <!-- 下一首 -->
        <button 
          class="control-btn secondary"
          @click.stop="nextSong"
          :disabled="!musicStore.hasNext"
        >
          <IonIcon :icon="playSkipForwardOutline" />
        </button>
      </div>
      
      <!-- 功能按钮 -->
      <div class="function-controls">
        <!-- 播放模式 -->
        <button 
          class="control-btn function"
          @click.stop="togglePlayMode"
          :title="playModeText"
        >
          <IonIcon :icon="playModeIcon" />
        </button>
        
        <!-- 喜欢 -->
        <button 
          class="control-btn function"
          @click.stop="toggleLike"
          :class="{ active: isLiked }"
        >
          <IonIcon :icon="isLiked ? heart : heartOutline" />
        </button>
        
        <!-- 音量控制 -->
        <div class="volume-control" @click.stop>
          <button class="control-btn function" @click="toggleVolumePanel">
            <IonIcon :icon="volumeIcon" />
          </button>
          <div v-if="showVolumePanel" class="volume-panel">
            <input 
              type="range" 
              min="0" 
              max="100" 
              :value="volumePercent"
              @input="setVolume"
              class="volume-slider"
            />
            <span class="volume-text">{{ volumePercent }}%</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed, ref } from 'vue'
import { IonIcon } from '@ionic/vue'
import {
  play,
  pause,
  heart,
  heartOutline,
  playSkipBackOutline,
  playSkipForwardOutline,
  repeat,
  shuffle,
  volumeHigh,
  volumeLow,
  volumeMute,
  volumeOff
} from 'ionicons/icons'
import { useMusicStore } from '@/stores/music'
import { useRouter } from 'vue-router'

const musicStore = useMusicStore()
const router = useRouter()

// 状态
const showVolumePanel = ref(false)
const playMode = ref<'order' | 'random' | 'repeat'>('order') // 播放模式

// 计算属性
const currentSong = computed(() => musicStore.currentSong)
const isPlaying = computed(() => musicStore.isPlaying)
const isLiked = computed(() => musicStore.isLiked(currentSong.value?.id))
const progressPercent = computed(() => musicStore.progress)
const volumePercent = computed(() => Math.round(musicStore.volume * 100))

// 播放模式相关
const playModeIcon = computed(() => {
  switch (playMode.value) {
    case 'repeat':
      return repeat
    case 'random':
      return shuffle
    default:
      return repeat
  }
})

const playModeText = computed(() => {
  switch (playMode.value) {
    case 'repeat':
      return '列表循环'
    case 'random':
      return '随机播放'
    default:
      return '顺序播放'
  }
})

// 音量图标
const volumeIcon = computed(() => {
  const volume = musicStore.volume
  if (volume === 0) return volumeOff
  if (volume < 0.3) return volumeLow
  if (volume < 0.7) return volumeHigh
  return volumeHigh
})

// 方法
const togglePlay = () => {
  if (!currentSong.value) {
    musicStore.setTestSong()
    return
  }
  musicStore.togglePlay()
}

const nextSong = () => {
  musicStore.nextSong()
}

const prevSong = () => {
  musicStore.prevSong()
}

const toggleLike = () => {
  if (currentSong.value) {
    musicStore.toggleLike(currentSong.value.id)
  }
}

const togglePlayMode = () => {
  const modes: Array<'order' | 'random' | 'repeat'> = ['order', 'random', 'repeat']
  const currentIndex = modes.indexOf(playMode.value)
  playMode.value = modes[(currentIndex + 1) % modes.length]
}

const toggleVolumePanel = () => {
  showVolumePanel.value = !showVolumePanel.value
}

const setVolume = (event: Event) => {
  const target = event.target as HTMLInputElement
  const volume = parseInt(target.value) / 100
  musicStore.setVolume(volume)
}

const openFullPlayer = () => {
  router.push('/player')
}

const handleImageError = (event: Event) => {
  const target = event.target as HTMLImageElement
  target.src = '/images/album.jpg'
}

const formatTime = (time: number) => {
  const minutes = Math.floor(time / 60)
  const seconds = Math.floor(time % 60)
  return `${minutes}:${seconds.toString().padStart(2, '0')}`
}
</script>

